pro-list\">\n                <a :href=\"'https://market.camagrimarket.org/kh/feed-detail/' + product.id\" target=\"_blank\">\n                  <div class=\"text-center\">\n                    <img :src=\"image_helper.methods.getMarketImage(product.thumnail)\" alt=\"image\" title=\"image\"\n                         class=\"pro-img img-responsive\"/>\n                  </div>\n                </a>\n                <div class=\"user-profile\">\n                  <!--  \"product.user_profile||-->\n                  <img src=\"/assets/img/user-market.jpg\" class=\"profile\" alt=\"\">\n                </div>\n                <div class=\"user-name\">\n                  <h4 class=\"name\">{{ product.user_first_name }} {{ product.user_last_name }}</h4>\n<!--                  <p class=\"date-time\" v-text=\"product.date\">Yesterday at 6:20 pm</p>-->\n                </div>\n                <div class=\"onhover1\" id=\"triangle-bottomright\">\n                  <div class=\"button-group\">\n                    <div class=\"user-type\">{{ product.user_type_name }}</div>\n                  </div>\n                </div>\n              </div>\n\n              <div class=\"caption text-center\">\n                <div class=\"row\">\n                  <h4>{{ product.type === 0 ? \"\" : \"\" }} : {{ product.commodity_name }}</h4>\n                  <div class=\"col-md-6 col-xs-6 pr\">\n                    <p class=\"price\"> :<span>{{ product.price + '/' + product.unit_name }}</span>\n                    </p>\n                    <p><i class=\"fa fa-calendar\" aria-hidden=\"true\"></i>{{ product.collect_date.substr(0, 10) }}</p>\n                  </div>\n                  <div class=\"col-md-6 col-xs-6 pl\">\n                    <p class=\"qty\"> :<span>{{ product.quantity }} {{ product.unit_name }}</span></p>\n                    <a :href=\"'https://market.camagrimarket.org/kh/feed-detail/' + product.id\" target=\"_blank\"\n                       class=\"read-more\">\n                      <p><i class=\"fa fa-arrow-right\" aria-hidden=\"true\"></i></p>\n                    </a>\n                  </div>\n                </div>\n              </div>\n            </div>\n          </div>\n        </div>\n      </div>\n\n      <div v-else>\n        <LvSkeleton\n            primaryColor=\"#f2f2f2\"\n            secondaryColor=\"#ffffff\"\n            width=\"100%\"\n            height=\"200\"\n        />\n      </div>\n    </div>\n\n  </section>\n</template>\n\n<style scoped>\n\n.product-thumb .image .pro-list {\n  position: relative;\n  overflow: hidden;\n}\n\n.product-thumb .image .pro-list .user-profile .profile {\n  top: 12px;\n  left: 12px;\n  width: 52px;\n  height: 52px;\n  border: 1px solid #fff;\n  border-radius: 50%;\n  position: absolute;\n}\n\n.product-thumb .image .pro-list .user-name {\n  bottom: 0;\n  left: 14px;\n  position: absolute;\n}\n\n.product-thumb .image .pro-list .user-name .name {\n  font-size: 14px;\n}\n\n.product-thumb .image .pro-list .user-name .date-time {\n  font-size: 10px;\n}\n\n.product-thumb .image .pro-list .onhover1 {\n  position: absolute;\n}\n\n.product-thumb .image .pro-list a::before {\n  content: \"\";\n  background: #707070;\n  opacity: 0.3;\n  height: 100%;\n  width: 100%;\n  position: absolute;\n  top: 0;\n  left: 0;\n  margin: 0 auto;\n}\n\n.user-type {\n  background: #0088AA;\n  padding: 5px 8px;\n  height: 28px;\n  border: none;\n  color: #fff;\n  font-size: 13px;\n  width: 80px;\n}\n\n#triangle-bottomright {\n  width: 0;\n  height: 0;\n  margin-right: 80px;\n  border-bottom: 28px solid #0088AA;\n  border-left: 20px solid transparent;\n}\n\n.read-more {\n  font-weight: 400;\n  font-size: 13px;\n  color: #0088AA;\n}\n\n\n.product-thumb .caption h4 {\n  font-size: 13px;\n  padding-top: 5px;\n}\n\n.product-thumb .caption .price {\n  font-size: 11px;\n}\n\n.product-thumb .caption p {\n  font-size: 11px;\n}\n\n.product-thumb .caption .qty {\n  font-size: 11px;\n}\n\n.product-thumb .caption .qty span {\n  font-size: 11px;\n}\n\n.product-thumb .caption .read-more {\n  font-size: 12px;\n}\n\n.product-thumb .caption .fa-arrow-right {\n  font-size: 9px;\n}\n\n.product-thumb .caption .pl {\n  margin-top: 7px;\n  padding-left: 5px;\n  text-align: right;\n}\n\n.product-thumb .caption .price {\n  font-size: 13px;\n  font-weight: 400;\n  color: #282828;\n  margin: 5px 0 0;\n}\n\n.product-thumb .caption .price span {\n  color: #f61c00;\n  padding-left: 5px;\n  font-weight: 500;\n  text-decoration: none;\n}\n\n.product-thumb .caption .fa-calendar {\n  color: #000;\n  font-size: 12px;\n  padding-right: 5px;\n}\n\n.pro-img {\n  height: 175px !important;\n}\n\n.product {\n  margin: 100px;\n}\n\n</style>\n"],"sourceRoot":""}]);
